.slide-bottom {
  position: relative;
  margin-top: -120px;
  z-index: 9999;
  height: 269px;

  .title:before {
    float: left;
    width: 100%;
    height: 40px;
    content: '';
    background: transparent url(../../img/fixed/title-1.png) right top no-repeat;
  }

  .title {
    display: inline-block;
    position: relative;
    width: 285px;

    h2 {
      float: left;
      width: 100%;
      background: #f4f4f4;
      margin: 0;
      text-align: center;
      font-size: 24px;
      line-height: 20px;
      text-transform: uppercase;
      color: #34343e;
      padding: 50px 0 130px;
      letter-spacing: -0.01em;
    }
  }
}

.deneme {
  font-size: 16px;
}